import React from "react";
import { Link } from "react-router-dom";
import "./Home.css";
const Home = (props) => {
  return (
    <>
      <button
        className="customerBtn"
        onClick={() => props.history.push("/postMainCustomer")}
      >
        고객
      </button>
      <button
        className="consultantBtn"
        onClick={() => props.history.push("/login")}
      >
        상담사
      </button>
    </>
  );
};

export default Home;
