import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import CommonTable from "../table/CommonTable";
import CommonTableColumn from "../table/CommonTableColumn";
import CommonTableRow from "../table/CommonTableRow";
import axios from "axios";
import "./Post.css";

const PostList = (props) => {
  const [dataList, setDataList] = useState([]);

  useEffect(() => {
    getInquerysList();
  }, []);

  const getInquerysList = () => {
    axios
      .get("http://localhost:8000/api/getInquerys")
      .then((response) => {
        setDataList(response.data.response);
      })
      .catch((Error) => {
        console.log(Error);
      });
  };

  const logout = () => {
    localStorage.removeItem("userInfo");
    props.history.push("/");
  };

  return (
    <>
      <button className="logoutBtn" onClick={logout}>
        메인화면으로
      </button>
      <div className="title">고객 문의 목록 (고객 전용)</div>
      <CommonTable
        headersName={[
          "글번호",
          "고객ID",
          "제목",
          "상담사ID",
          "등록일",
          "상담사 답변",
        ]}
      >
        {dataList
          ? dataList.map((item, index) => {
              return (
                <CommonTableRow key={index}>
                  <CommonTableColumn>{item.id}</CommonTableColumn>
                  <CommonTableColumn>{item.userId}</CommonTableColumn>
                  <CommonTableColumn>
                    <Link
                      to={{
                        pathname: `/PostView`,
                        state: {
                          userId: item.userId,
                          title: item.title,
                          content: item.content,
                          counselorId: item.counselorId,
                          createdAt: item.createdAt,
                          updatedAt: item.updatedAt,
                        },
                      }}
                    >
                      {item.title}
                    </Link>
                  </CommonTableColumn>
                  <CommonTableColumn>
                    {item.counselorId == null ? `없음` : item.counselorId}
                  </CommonTableColumn>
                  <CommonTableColumn>{item.createdAt}</CommonTableColumn>
                  <CommonTableColumn>
                    {item.isAnswered == "N" ? (
                      "답변없음"
                    ) : (
                      <Link
                        to={{
                          pathname: `/PostView`,
                          state: {
                            userId: item.userId,
                            title: item.title,
                            content: item.content,
                            counselorId: item.counselorId,
                            createdAt: item.createdAt,
                            updatedAt: item.updatedAt,
                          },
                        }}
                      >
                        답변보기
                      </Link>
                    )}
                  </CommonTableColumn>
                </CommonTableRow>
              );
            })
          : ""}
      </CommonTable>
    </>
  );
};

export default PostList;
